* {
  box-sizing: border-box;
}

body {
  background: #f5f6f9;
  margin: 0;
  font-family: Poppins, sans-serif;
  font-size: 16px;
}

header {
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 3px 10px #5077f24d;
}

h1 {
  text-align: center;
  margin: 0;
}

.inputSearch {
  justify-content: center;
  width: 100%;
  max-width: 600px;
  margin-top: 10px;
  display: flex;
}

form {
  border: 1.5px solid red;
  border-radius: 35px;
  align-items: center;
  width: 100%;
  height: 40px;
  display: flex;
  overflow: hidden;
}

.inputSearch input {
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  padding: 0 10px;
  font-size: 16px;
}

.inputSearch span {
  color: #fff;
  background-color: #ffc300;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 100%;
  font-size: 18px;
  display: flex;
}

.menuBtn {
  cursor: pointer;
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-size: 20px;
  display: flex;
}

.menuBtn:hover {
  color: #fff;
  background-color: #ffc300;
}

.desktop {
  display: none;
}

.mobile {
  background-color: #f5f6f9;
  width: 100%;
  height: calc(100% - 150px);
  padding: 20px;
  position: absolute;
  top: 75px;
}

.mobile nav, .mobile nav ul {
  flex-direction: column;
}

.mobile .inputSearch {
  width: 90%;
  height: 35px;
}

.hidden {
  display: none;
}

main {
  flex-wrap: wrap;
  gap: 25px;
  margin: auto;
  padding: 10px;
  display: flex;
}

.card {
  background: #fff;
  border-radius: 5px;
  flex-direction: column;
  max-width: 400px;
  padding-bottom: 10px;
  display: flex;
  overflow: hidden;
  box-shadow: 7px 7px 25px 1px #0000000f;
}

.card img {
  background-color: #ffc300;
  min-height: 200px;
  max-height: 210px;
}

.card h4, .card div {
  padding: 5px 10px;
}

.publishbyDate {
  color: #a5a1ac;
  align-items: center;
  display: flex;
}

.publishbyDate p {
  margin: 0;
  font-size: 14px;
}

.publishbyDate span {
  padding: 0 10px;
}

.desc {
  -webkit-box-orient: vertical;
  padding-bottom: 10px;
  display: -webkit-box;
  overflow: hidden;
}

.card a {
  color: inherit;
  text-decoration: none;
}

@media (width >= 700px) {
  header {
    flex-direction: row;
    justify-content: space-between;
    padding: 5px 30px;
  }

  .desktop {
    display: flex;
  }

  .menuBtn {
    display: none;
  }

  .inputSearch {
    width: auto;
  }

  main {
    justify-content: center;
    padding: 20px;
  }

  .card h4, .card div {
    padding: 5px 15px;
  }
}
